@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
  box-sizing: border-box;   
}
body{
    background-color: #111;
    color: #FFF;
    margin: 0;
    font-family: 'Roboto', sans-serif;

}
.lists {
  margin-top: -150px;
}
footer {
  margin:  50px;
  text-align:  center;
}
.movieRow {
    margin-bottom: 30px;
}
.movieRow h2 {
    margin: 0px 0px 0px 30px;
}
.movieRow--listarea {
    overflow-x: hidden;
    padding-left: 30px;

}
.movieRow--item {
    display: inline-block;
    width: 150px;
    cursor:pointer; 
}
.movieRow--item img {
    width: 100%;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    transition: all ease 0.2s;
}
.movieRow--item img:hover {
    -webkit-transform: scale(1);
            transform: scale(1);
}
.movieRow--left,
.movieRow--right{
    position: absolute;
    width: 40px;
    height: 225;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.5;
}
.movieRow--left {
    left: 0;
}
.movieRow--right {
    right: 0;
}
.movieRow:hover .movieRow--left,
.movieRow:hover .movieRow--right {
    opacity: 1;
}
.featured {
    height: 100vh;
}
.featured--vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);

}
.featured--horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-bottom: 150px;
    padding-top: 70px;
} 
.featured--name {
    font-size: 60px;
    font-weight: bold;
}
.featured--info {
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;
}
.featured--points, 
.featured--year, 
.featured--seasons{
    display: inline-block;
    margin-right: 15px;
}
.featured--points {
    color: #46d369;
}
.featured--description {
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;
}
.featured--buttons {
    margin-top: 15px;
}
.featured--watchbutton, 
.featured--mylistbutton {
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 12px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease 0.2s;
}
.featured--watchbutton:hover, 
.featured--mylistbutton:hover {
    opacity: 0.7;
}    
.featured--watchbutton {
    background-color: #FFF;
    color: #000;
}
.featured--mylistbutton {
    background-color: #333 ;
    color: #FFF;
}
.featured--genres {
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}
header {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    background: transparent;
    transition: all ease 0.5s;

}
header.black {
    background-color: #141414;
}
.header--logo {
    height: 30px;
}
.header--logo img {
    height: 100%;

}
